	module.exports = {"data":{"wordpressPost":{"title":"Most read articles in February","content":"<p>The most read article in February was Kripke<em> et al</em>&#8216;s study of the association between hypnotics and mortality, which was only published on February 27th (attracting almost 17,000 views in just a few days) and received widespread press attention. The second most read was Townsend and Pitchford&#8217;s study comparing the effect of different weaning styles on BMI in later life, an article which also garnered international attention. Other popular papers published last month included Noble <em>et al</em>&#8216;s feasibility study of geospatial mapping and Stoolmiller <em>et al</em>&#8216;s analysis of media consumption and alcohol use.</p>\n<table border=\"1\" cellspacing=\"0\" cellpadding=\"0\" width=\"483\">\n<tbody>\n<tr>\n<td width=\"50\" valign=\"top\">Rank</td>\n<td width=\"132\" valign=\"top\">Author(s)</td>\n<td width=\"301\" valign=\"top\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Kripke et al</td>\n<td width=\"301\" valign=\"top\"><a href=\"http://bmjopen.bmj.com/content/2/1/e000850.full\">Hypnotics&#8217; association with mortality or cancer: a matched cohort study</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Townsend and Pitchford</td>\n<td width=\"301\" valign=\"top\"><a href=\"http://bmjopen.bmj.com/content/2/1/e000298.full\">Baby knows best? The impact of weaning style on food preferences and body mass index in early childhood in a case–controlled sample</a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Margel &amp; Fleshner</td>\n<td width=\"301\" valign=\"top\"><a href=\"http://bmjopen.bmj.com/content/1/2/e000311.full\">Oral contraceptive use is associated with prostate cancer: an ecological study</a></td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Noble et al</td>\n<td width=\"301\" valign=\"top\"><a href=\"http://bmjopen.bmj.com/content/2/1/e000711.full\">Feasibility study of geospatial mapping of chronic disease risk to inform public health commissioning</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Stoolmiller et al</td>\n<td width=\"301\" valign=\"top\"><a href=\"http://bmjopen.bmj.com/content/2/1/e000543.full\">Comparing media and family predictors of alcohol use: a cohort study of US adolescents</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Lee et al</td>\n<td width=\"301\" valign=\"top\"><a href=\"http://bmjopen.bmj.com/content/1/2/e000269.full?sid=cb490a28-a3b0-49b7-91d8-c1d9723dfc1c\">UK stroke incidence, mortality and cardiovascular risk management 1999–2008: time-trend analysis from the General Practice Research Database</a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Rajavashisth et al</td>\n<td width=\"301\" valign=\"top\"><a href=\"http://bmjopen.bmj.com/content/2/1/e000494.full.pdf+html\">Decreased prevalence of diabetes in marijuana users: cross-sectional data from the National Health and Nutrition Examination Survey (NHANES) III</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Crawley et al</td>\n<td width=\"301\" valign=\"top\"><a href=\"http://bmjopen.bmj.com/content/1/2/e000252.full\">Unidentified Chronic Fatigue Syndrome/myalgic encephalomyelitis (CFS/ME) is a major cause of school absence: surveillance outcomes from school-based clinics</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Bito et al</td>\n<td width=\"301\" valign=\"top\"><a href=\"http://bmjopen.bmj.com/content/2/1/e000371.full\">Randomised controlled trial evaluating the efficacy of wrap therapy for wound healing acceleration in patients with NPUAP stage II and III pressure ulcer</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">de Walque et al</td>\n<td width=\"301\" valign=\"top\"><a href=\"http://bmjopen.bmj.com/content/2/1/e000747.full\">Incentivising safe sex: a randomised trial of conditional cash transfers for HIV and sexually transmitted infection prevention in rural Tanzania</a></td>\n</tr>\n</tbody>\n</table>\n<p>Click on the titles to view the articles in full.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"dc25f630-f3f2-5330-ba0c-ec9b98107cb7"}}

